import CloseIcon from '@mui/icons-material/Close'
import { Box, Button, Card, Typography } from '@mui/material'

import type { Applicant } from 'src/models/Applicant'

import BulkActions from 'src/content/manage/Recruit/Applicants/BulkActions'

import { useGetForms } from 'src/hooks/api/forms'

interface ApplicantFormProps {
  visibleApplicant: Applicant | null
  clubId: number
  recruitId: number
  handleCloseForm: React.MouseEventHandler<HTMLButtonElement>
}

const ApplicantForm: React.FC<ApplicantFormProps> = ({ visibleApplicant, clubId, recruitId, handleCloseForm }) => {
  const { data: forms } = useGetForms(clubId)

  return (
    <Card sx={{ minHeight: '100%', overflowY: 'auto' }}>
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', pt: 1 }}>
        <Button variant="text" onClick={handleCloseForm} color="primary">
          <CloseIcon />
        </Button>
      </Box>
      {visibleApplicant && (
        <Box sx={{ height: '500px', width: '100%', padding: 2, overflowY: 'auto' }}>
          <Box justifyContent="center">
            <Typography variant="h3" sx={{ textAlign: 'center' }}>
              {visibleApplicant.name} 님 의 지원서
            </Typography>
            <Typography variant="subtitle2" sx={{ textAlign: 'center' }}>
              {visibleApplicant.studentId} / {visibleApplicant.department}
            </Typography>
            <Typography variant="subtitle2" sx={{ mb: 2, textAlign: 'center' }}>
              {visibleApplicant.phoneNumber} / {visibleApplicant.email}
            </Typography>
          </Box>

          {forms?.questions?.map((question, index) => (
            <>
              <Typography variant="h5" sx={{ mb: 1 }}>
                {question}
              </Typography>
              <Card
                elevation={0}
                sx={{ p: 1, mb: 2, border: 1, borderRadius: 1, borderColor: 'divider', minHeight: 40 }}
              >
                {visibleApplicant.answers?.[index]}
              </Card>
            </>
          ))}
          <Box display="flex" justifyContent="center">
            <BulkActions
              selectedApplicants={[visibleApplicant.id]}
              clubId={clubId}
              recruitId={recruitId}
              currentResultType={visibleApplicant.result}
            />
          </Box>
        </Box>
      )}
    </Card>
  )
}

export default ApplicantForm
